export default {
	data() {
		const date = new Date();
		const years = [];
		const year = date.getFullYear();
		const months = [];
		const month = date.getMonth() + 1;
		const days = [];
		const day = date.getDate();
		for (let i = 2019; i <= date.getFullYear(); i++) {
			years.push(i);
		}
		for (let i = 1; i <= 12; i++) {
			months.push(i);
		}
		for (let i = 1; i <= 31; i++) {
			days.push(i);
		}
		return {
			clip: '全部',
			childBirthday: '',
			isQuan: false,
			showTop: false,
			years,
			year,
			months,
			month,
			days,
			day,
			value: [9999, month - 1, day - 1],
		};
	},

	methods: {
		gotoTX() {
			uni.navigateTo({
				url: '../TiXian/TXorder'
			});
		},
		gotoCard() {
			uni.navigateTo({
				url: '../TiXian/MyCard'
			});
		},
		gotoUrl() {
			uni.navigateTo({
				url: '../TiXian/TXrecord'
			});
		},
		IFtop(index) {
			if (index == 1) {
				// 取消
				console.log('取消le');
			} else if (index == 2) {
				// 确定
				this.clip = this.year+'-'+this.month;
			}
			this.isshowtop();
		},
		isshowtop() {
			this.showTop = !this.showTop;
		},
		bindChange: function(e) {
			// console.log(e.detail.value)
			const val = e.detail.value;
			this.year = this.years[val[0]];
			this.month = this.months[val[1]];
			this.day = this.days[val[2]];
			console.log(this.year+'-'+this.month)
		}
	}
};
